.PageTitle {
  font-size: 18px;
  font-weight: 600;
  padding: 16px 0 0;
}
.FamilyTable {
  padding: 16px 0 0;
}
.buttonsDiv {
  padding: 16px 0 0;
}
.continueButton {
  text-align: center;
  padding: 8px 0 0;
}

.newFamily .modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.newFamily .modal {
  background: white;
  border-radius: 5px;
  width: 40%;
  height: 70%;
}

.newFamily .modal-header {
  /* display: flex; */
  justify-content: space-between;
  text-align: center;
  border-bottom: 2px solid #DDDBDA;
 
}
.newFamily .modal-body {
  width: 100%;
  height: 40%;
  overflow-y: scroll;
  border-bottom: 2px solid #DDDBDA;
  position: relative;
}
.newFamily .account-details {
  display: flex;
  flex-wrap: wrap;
}
.newFamily .custom-select {
  min-width: 45%;
  position: relative;
}
form div {
  margin-top: 1rem;
}

